@font-face {
  font-family: "Montserrat";
  src: local("Montserrat"),
    url("./assets/fonts/Montserrat-Regular.ttf") format("woff");
  font-display: swap;
}

:root {
  --bg-color: #ffffff;
  --text-color: #1a1a1a;
  --button-bg: #f0f0f0;
  --svg-color: #000000;
  --sep-color: #000000;
  --invert-img: invert(0);
}

[data-theme='dark'] {
  --bg-color: #121212;
  --text-color: #f5f5f5;
  --button-bg: #333333;
  --svg-color: #ffffff;
  --sep-color: #ffffff;
  --invert-img: invert(1);
}

body {
  margin: 0;
  font: 19px / 23px Montserrat, "Montserrat", sans-serif;
  font-size: calc(1vw + 1vh);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg-color);
  color: var(--text-color);
  transition: background-color 0.3s ease, color 0.3s ease;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.welcome {
  margin: 10vw; /* Top, right, bottom, left */
}

.intro {
  display: grid;
  grid-template-columns: 1fr auto;
}

.profile {
  border-radius: 10px;
  width: 20vw;
  height: auto;
}

.greeting {
  color: red;
  font-weight: bold;
  line-height: 1.2;
  align-items: end;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 3.8vw;
}

.icon-container {
  margin-bottom: 1em;
}

.separator-small {
  margin: 0.5em 0;
  border: none;
  border-top: 2px solid var(--sep-color);
  width: 95%;
  transition: border-top 0.3s ease;
}

.biography {
  max-width: 30ch;
  line-height: 1.2;
}

.title {
  color: red;
}

.info {
  font-size: 1em;
  margin: 0;
}

.text {
  font-style: italic;
  line-height: 1.0;
}

.separator {
  margin: 0.5em 0;
  border: none;
  border-top: 2px solid var(--sep-color);
  transition: border-top 0.3s ease;
}

.column {
  display: flex;
  align-items: center;
}

.table {
  display: grid;
  grid-template-columns: 1fr auto;
  width: 90%;
}

.description {
  max-width: 50vw;
}

.indent {
  padding-left: 5vw;
}

.indent-large {
  margin-left: 5vw;
  width: 80%;
}

.icon {
  width: 2em;
  height: 2em;
  margin: 0 0.5em 0 0;
  fill: black;
  transition: all 0.5s ease;
}
.icon:hover {
  fill: red;
}
svg path {
  fill: var(--svg-color);
  transition: fill 0.3s ease;
}
.invertimg {
  filter: var(--invert-img);
  transition: filter 0.3s ease;
}

.phone {
  width: 1em;
  height: 1em;
  margin: 0 0.5em 0 0;
  fill: black;
  transition: all 0.5s ease;
}
.phone:hover {
  fill: greenyellow;
}

.message {
  width: 1em;
  height: 1em;
  margin: 0 0.5em 0 0;
  fill: black;
  transition: all 0.5s ease;
}
.message:hover {
  fill: greenyellow;
}

.email {
  width: 1em;
  height: 1em;
  margin: 0 0.5em 0 0;
  fill: black;
  transition: all 0.5s ease;
}
.email:hover {
  fill: #0077b6;
}

.location {
  width: 1em;
  height: 1em;
  margin: 0 0.5em 0 0;
  fill: black;
  transition: all 0.5s ease;
}
.location:hover {
  fill: red;
}

.button {
  background-color: var(--button-bg);
  border: none;
  border-radius: 10px;
  padding: 10px;
  margin: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.no-style {
  text-decoration: none;
  color: inherit;
}